export default {
	data() {
		return {
			is_click:false
		};
	},
	methods: {
		save() {





			

			const that = this
			this.is_click = true
			uni.showLoading({
				title: '加载中'
			});
			console.log(1)
			uni.authorize({
				scope: 'scope.writePhotosAlbum',
				success(res) {
					uni.downloadFile({
						url: 'https://www.baidu.com/img/bd_logo1.png', //仅为示例，并非真实的资源
						success: (res) => {
							console.log(res)
							uni.saveImageToPhotosAlbum({
								filePath: res.tempFilePath,
								success: function(res) {
									console.log('save success', res);
									uni.showToast({
										title: '图片保存成功',
										duration: 2000
									})
									that.is_click = false
									uni.hideLoading();
								}
							});
						}
					});
			
				}
			})
			


			

		},
	},
	onPullDownRefresh() {
		//this._load()
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 2000);
	}
}
